import './Card.css';
import mystery from './../images/mystery.png';

const Card = ({color, flipCard, order , id}) => {
  return (
    <div className='card' key={id} id={id} color={color} onClick={flipCard} style={{order: order}}>
      <div className="front">
        <img src={mystery} alt="?" />
      </div>
      <div className={`back bg-${color}`}>
        
      </div>
    </div>
  )
}

export default Card
