import './LeadBoard.css';
import { useState, useEffect } from 'react';

const LeadBoard = ({ leadersData }) => {
  const [leaders, setLeaders] = useState([]);

  useEffect(() => {
    
    for(let i = 0; i < leadersData.length; i++) {
      const {name, score} = leadersData[i];
      const obj = <Leader key={i} name={name} score={score}/>
      setLeaders([...leaders, obj]);
    }
    // leadersData.forEach((leader) => {
    //   const obj = <Leader name={leader.name} score={leader.score} />;
    //   setLeaders([...leaders, obj]);
    // });
  }, [leadersData]);

  return (
    <div className='lead-board'>
      <div className='heading'>Leader Board</div>
      <div className='leaders'>{leaders.map((leader) => leader)}</div>
    </div>
  );
};

export default LeadBoard;

export const Leader = ({ name, score }) => {
  return (
    <div className='leader'>
      <div className='name'>{name}</div>
      <div className='score'>{score}</div>
    </div>
  );
};
