import Header from './components/Header';
import Cards from './components/Cards';
import LeadBoard, { Leader } from './components/LeadBoard';

import { useState, useEffect } from 'react';

function App() {
  const [wrongTries, setWrongTries] = useState(0);
  const increaseWrongTries = () => {
    setWrongTries(wrongTries + 1);
  };

  const [leadersData, setLeadersData] = useState([]);
  const [name, setName] = useState('user');
  const newScore = (score, name) => {
    const obj = {
      name: name,
      score: score,
    }
    setLeadersData([...leadersData, obj])
  };

  useEffect(() => {
    setTimeout(() => {
      setName(prompt('enter your name'));
    }, 1500)
  },[leadersData])

  return (
    <div className='app'>
        <Header wrongTries={wrongTries} />
      <div className='split'>
        <Cards
          setWrongTries={setWrongTries}
          wrongTries={wrongTries}
          newScore={newScore}
          name={name}
        />
        <LeadBoard leadersData={leadersData}/>
      </div>
    </div>
  );
}

export default App;
