import './Cards.css';
import Card from './Card';
import { useState } from 'react';

const colors = [
  'red',
  'blue',
  'yellow',
  'green',
  'pink',
  'purple',
  'red',
  'blue',
  'yellow',
  'green',
  'pink',
  'purple',
];

const randomOrder = (length) => {
  let randomIndexes = [];
  while (length > 0) {
    let random = Math.floor(Math.random() * length);
    randomIndexes.push(random);
    length--;
  }
  return randomIndexes;
};







const Cards = ({ setWrongTries, wrongTries, newScore, name}) => {

  const [randoms, setRandoms] = useState(randomOrder(colors.length));
  
  const setCards = (colors, randoms) => {
    let cards = [];
    for (let i = 0; i < colors.length; i++) {
      cards.push(
        <Card key={i} id={i} color={colors[i]} order={randoms[i]} flipCard={flipCard} />
      );
    }
    return cards;
  };

  const [startTime, setStartTime] = useState();

  const flipCard = (e) => {
    const card = e.target.parentNode;
    const cards = Array.from(document.querySelectorAll('.cards .card'));

    //function to remove flipped card
    const removeFlip = (cards) => {
      const removedCards = cards.filter(
        (card) => !card.classList.contains('right')
      );
      removedCards.forEach((card) => (card.style.pointerEvents = 'none'));
      setTimeout(() => {
        removedCards.forEach((card) => card.classList.remove('flip'));
        removedCards.forEach((card) => (card.style.pointerEvents = 'auto'));
      }, 500);
    };
    
    //function to get filtered cards
    const getFilteredCards = (cards) => {
      return cards.filter(
        (card) =>
          card.classList.contains('flip') && !card.classList.contains('right')
      );
    } 

    //function to get all rights cards 
    const getRightCards = (cards) => {
      return cards.filter(card => card.classList.contains('right'));
    }

    //function to reset the game 
    const reset = () => {
      cards.forEach(card => {
        card.className = 'card';
        card.style.pointerEvents = 'auto';
      });
      setWrongTries(0);
      setRandoms(randomOrder(colors.length));
    }

    //if it's the first card
    if(cards.filter(card => card.classList.contains('first')) <= 0) {
      setStartTime(new Date());
      console.log(startTime);
      card.classList.add('first');
    }

    // get cards that are not correct
    const filteredCards = getFilteredCards(cards);
    card.style.pointerEvents = 'none';
    //if it's the first of the two flip it
    if (filteredCards.length === 0) {
      card.classList.add('flip');
    } 
    // if it's the second: 
    else{
      // set needed variables
      const one = filteredCards[0],
            two = card;

      card.classList.add('flip');
      
      // check if cards are macthed
      const matchCards = (one, two) => {
        const firstColor = one.getAttribute('color');
        const secondColor = two.getAttribute('color');
        const firstId = one.getAttribute('id');
        const secondId = two.getAttribute('id');

        return (firstColor === secondColor && firstId !== secondId) ;
      }

      if (matchCards(one, two)) {

        one.classList.add('right');
        card.classList.add('right');
        
        //if all cards are flipped (user won)
        if (getRightCards(cards).length === 12) {

          window.setTimeout(() => {
            const endTime = new Date();
            const timeInSeconds = (endTime - startTime) / 1000;
            const score = Math.floor(1000/(wrongTries + (timeInSeconds / 3)));
            console.log(timeInSeconds, wrongTries);
            console.log(score);
            newScore(score, name);
          }, 200);

          // reset function ;
          setTimeout(() => {
            reset();
          }, 1000);
        }
      } 
      //if it's the wrong card pair
      else {
        card.classList.add('flip');
        //set time out
        //remove all flipped
        removeFlip(cards);
        setWrongTries(wrongTries + 1);
      }
    }
  };


  const cards = setCards(colors, randoms);

  return <div className='cards'>{cards.map((card) => card)}</div>;
};

export default Cards;
